import React, { useState } from "react";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import { useTheme } from "@mui/material/styles";
import cl from "./slider.module.scss";
import MobileStepper from "@mui/material/MobileStepper";
import card from "../../../img/ava.jpg";
import left from "../../../img/left.svg";
import plus from "../../../img/plus.svg";
import right from "../../../img/right.svg";
import Form from "../../Form/Form";
import { useMediaQuery } from "@mui/material";

const banners = [
  {
    id: 1,
    slides: [
      {
        id: 12,
        img: card,
        title: " ",
        date: "08.01.2022",
        description:
          " ,            .  KPI        -      ,    .  -   ,     ,   .    .,      ",
      },
      {
        id: 13,
        img: card,
        title: " ",
        date: "08.01.2022",
        description:
          "   ,   ,      .      ,      ,    .      .         .",
      },
    ],
  },
  {
    id: 2,
    slides: [
      {
        id: 22,
        img: card,
        title: " ",
        date: "26.12.2021",
        description:
          "    ilink  .      :    ,   ,   .        ,    ,  ,   .     .  ,      ilink.",
      },
      {
        id: 23,
        img: card,
        title: " ",
        date: "16.12.2021",
        description:
          "       ,      HR-    .       HR-.   .  ,   .    .   ,   ,    .     .  , .",
      },
    ],
  },
];

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);
const Slider = ({ showAnswer, setShowAnswer }) => {
  const isMob = useMediaQuery("(max-width:768px");
  const theme = useTheme();
  const [activeStep, setActiveStep] = useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  const handleStepChange = (step) => {
    setActiveStep(step);
  };
  const [showform, setShowform] = useState(false);
  return (
    <div className={cl.SliderPage}>
      <div className={cl.otzyvy}>
        <div className={cl.otzyvHead}>
          <h2></h2>
          <button
            onClick={() => setShowform(!showform)}
            style={{ cursor: "pointer" }}
          >
            <img src={plus} />
            <p style={isMob ? { display: "none" } : { display: "block" }}>
               
            </p>
          </button>
        </div>
        <div className={cl.Slider}>
          <AutoPlaySwipeableViews
            autoplay={false}
            axis={theme.direction === "rtl" ? "x-reverse" : "x"}
            index={activeStep}
            onChangeIndex={handleStepChange}
            enableMouseEvents
            style={{ marginBottom: "30px" }}
            className={cl.Swipe}
          >
            {banners.map((_step, index) => {
              const { id, slides } = _step;

              return isMob ? (
                slides.map((step, index) => (
                  <div
                    key={index}
                    style={{ overflow: "hidden" }}
                    className={cl.slider_Mob}
                  >
                    <div>
                      <div>
                        <img src={step.img} className={cl.img} />
                        <h3>{step.title}</h3>
                      </div>
                      <p>{step.date}</p>
                    </div>
                    <p>{step.description}</p>
                  </div>
                ))
              ) : (
                <div className={!isMob && cl.slider}>
                  {slides.map((step, index) => (
                    <div
                      key={index}
                      style={{ overflow: "hidden" }}
                      className={cl.slider_left}
                    >
                      <div>
                        <div>
                          <img src={step.img} className={cl.img} />
                          <h3>{step.title}</h3>
                        </div>
                        <p>{step.date}</p>
                      </div>
                      <p>{step.description}</p>
                    </div>
                  ))}
                </div>
              );
            })}
          </AutoPlaySwipeableViews>
          <MobileStepper
            variant="dots"
            steps={isMob ? 4 : 2}
            position="static"
            activeStep={activeStep}
          />
        </div>
      </div>
      <div
        className={cl.cont}
        style={isMob ? { display: "none" } : { display: "block" }}
      >
        <div className={cl.buttons_cont}>
          <button
            className={cl.buttons}
            onClick={handleBack}
            disabled={activeStep === 0}
          >
            <img src={left} />
          </button>
          <button
            className={cl.buttons}
            onClick={handleNext}
            disabled={activeStep === banners.length - 1}
          >
            <img src={right} />
          </button>
        </div>
      </div>
      {showform ? (
        <Form
          answer={showAnswer}
          setAnswer={setShowAnswer}
          show={showform}
          setShow={setShowform}
        />
      ) : null}
    </div>
  );
};

export default Slider;
